<template>
  <div id="report">
    <section class="report-item">
      <h3>请简述举报对方的原因</h3>
      <textarea
        rows="8"
        placeholder="平台将全面保护您的隐私，请放心填写！"
        maxlength="100"
        @input="reasonHandler"
      ></textarea>
      <p>{{reason.length}}/100</p>
    </section>
    <section class="report-btn">
      <button @click="submitHandler">匿名提交</button>
    </section>
    <dl class="report-statement">
      <dt>平台承诺</dt>
      <dd>我们是一个健康的交友社区，若您受到侵犯，请放心填写举报原因，平台将全力保护您的权益！</dd>
    </dl>
  </div>
</template>

<script>

/**
 * Created by gugaofeng on 17/6/5.
 */


(function() {

    /*
     *客户端参考wiki https://192.168.40.112/ClientDev/sharing/wiki/新玩法和JS交互协议
     */



    function setupWebViewJavascriptBridge(callback) {
        //Console.log("typeof_WebViewJavascriptBridge___" + typeof window.WebViewJavascriptBridge);
        //if(jsBridge != undefined){return}
        if (window.WVJBCallbacks)
        { 
            return window.WVJBCallbacks.push(callback); 
        }
        window.WVJBCallbacks = [callback];
        var WVJBIframe = document.createElement('iframe');
        WVJBIframe.style.display = 'none';
        WVJBIframe.src = "inkejs://__bridge_loaded__"; // 'https://__bridge_loaded__';
        document.documentElement.appendChild(WVJBIframe);
        setTimeout(function() { document.documentElement.removeChild(WVJBIframe) }, 0);
    }

    /*
    setupWebViewJavascriptBridge(function(bridge) {
        var uniqueId = 1
        function log(message, data) {
            var log = document.getElementById('log')
            var el = document.createElement('div')
            el.className = 'logLine'
            el.innerHTML = uniqueId++ + '. ' + message + ':<br/>' + JSON.stringify(data)
            if (log.children.length) { log.insertBefore(el, log.children[0]) }
            else { log.appendChild(el) }
        }
        //注册函数 让客户端调用
        bridge.registerHandler('testJavascriptHandler', function(data, responseCallback) {
            log('ObjC called testJavascriptHandler with', data)
            var responseData = { 'Javascript Says':'Right back atcha!' }
            log('JS responding with', responseData)
            responseCallback(responseData)
        })

        document.body.appendChild(document.createElement('br'))

        var callbackButton = document.getElementById('buttons').appendChild(document.createElement('button'))
        callbackButton.innerHTML = 'Fire testObjcCallback'
        callbackButton.onclick = function(e) {
            e.preventDefault()
            log('JS calling handler "testObjcCallback"')
            //js调用客户端函数
            bridge.callHandler('testObjcCallback', {'foo': 'bar'}, function(response) {
                log('JS got response', response)
            })
        }
    });
    //*/

    function log() {

    }



    var userAgent = window.navigator.userAgent;
    var IsIos = /iPhone|mac|iPod|iPad/i.test(userAgent);
    var isAndroid = /android/i.test(userAgent);


    var JsBridge = {
        //打开页面链接 { url : ''}
        open_page: function(param) {
            //Console.log("open_page_start");
            var param = param || null;
            setupWebViewJavascriptBridge(function(bridge) {
                //Console.log("setupWebViewJavascriptBridge callback");
                bridge.callHandler('open_page', param, function(response) {
                    log('JS got response', response);
                })
            });
        },
        //关闭窗口
        close_page: function(param) {
            var param = param || null;
            //alert("close");
            //Console.log("close_page_start");
            setupWebViewJavascriptBridge(function(bridge) {
                //Console.log("typeof_bridgeCallHandler___" + typeof bridge.callHandler);
                bridge.callHandler('close_page', param, function(response) {
                    //Console.log("response__" + response);
                })
            });
        },
        //showToast
        showToast: function(param) {
            var param = param || null;
            //alert("close");
            setupWebViewJavascriptBridge(function(bridge) {
                ////Console.log("typeof_bridgeCallHandler___" + typeof bridge.callHandler);
                bridge.callHandler('jsBridge.showToast', param, function(response) {
                    ////Console.log("response__" + response);
                })
            });
        },
        //确认窗口
        confirmDialog: function(param) {
            var param = param || null;
            //alert("close");
            setupWebViewJavascriptBridge(function(bridge) {
                ////Console.log("typeof_bridgeCallHandler___" + typeof bridge.callHandler);
                bridge.callHandler('jsBridge.confirmDialog', param, function(response) {
                    ////Console.log("response__" + response);
                })
            });
        },
        /*
          {
             "msg": "分享说明",
             "url": "分享地址",
             "title": "分享标题",
             "image": "分享图片地址",
             "platform": [   // 自由组合，不传值使用默认的（以下全部）
                 "qq",       // QQ
                 "qzone",    // QQ空间
                 "wechat",   // 微信
                 "moments",  // 微信朋友圈
                 "weibo",    // 微博
                 "url"       // 复制链接
             ]
         }
         */
        share: function(param) {
            if (!param) {
                return;
            }
            var opt = { "action": "shareUrl", data: param };
            if (IsIos && window.webkit && webkit.messageHandlers) {
                window.webkit.messageHandlers.sendInkeNativeInfo.postMessage(JSON.stringify(opt));
            } else if (isAndroid && window.InKeJs) {
                window.InKeJs.sendInkeNativeInfo(JSON.stringify(opt));
            } else {
                //Console.log("shareUrl_is_fail");
            }


        }

    };

    window.JsBridge = JsBridge;
})();


export default {
  data () {
    return {
      reason: '',
      mail: ''
    }
  },
  computed: {},
  methods: {
    reasonHandler (e) {
      this.reason = e.target.value
    },

    submitHandler () {
      if((!this.reason) && location.search.indexOf("iPhone") != -1){
        alert('请完善举报信息')
        window.JsBridge.showToast('请完善举报信息')
        return false
      }

      if((!this.reason) && location.search.indexOf("iPhone") == -1){
        alert('请完善举报信息')
        jsBridge.showToast('请完善举报信息')
        return false
      }

      this.$http.post('http://h5.anyueclub.com/api/report/add'+location.search, {
        reason: this.reason,
        reported_id: parseInt(this.getUrlQuery('reported_id')),
        uid: this.getUrlQuery('uid')
      }).then((res) => {
        alert('已收到您的举报，我们会尽快处理')
        if(location.search.indexOf("iPhone") != -1){
          window.JsBridge.confirmDialog('已收到您的举报，我们会尽快处理');
        }else{
          jsBridge.confirmDialog('已收到您的举报，我们会尽快处理');
        }
      }).catch((err) => {
        alert('举报失败，请重试～')
        if(location.search.indexOf("iPhone") != -1){
          window.JsBridge.showToast(err.error_msg || '举报失败，请重试～');
        }else{
          jsBridge.showToast(err.error_msg || '举报失败，请重试～');
        }
      })
    },
    getUrlQuery (name) {
      let reg = new RegExp('(^|&)' + name + '=([^&]*)(&|$)', 'i')
      let search = location.search.substring(1)
      if (!search) return false
      let r = search.replace('#', '').match(reg)

      if (r != null) {
        let unescapeStr = unescape(r[2])
        switch (unescapeStr) {
          case 'true':
            return true
          case 'null':
            return null
          case 'false':
            return false
          case 'undefined':
            return undefined
          default:
            return unescapeStr
        }
      } else {
        return null
      }
    }
  }
}
</script>

<style scoped>
#report {
  width: 100%;
  background-color: #f7f7f7;
}

.report-item {
  padding-bottom: 20px;
}

.report-item h3 {
  padding-left: 30px;
  line-height: 90px;
  font-size: 30px;
  color: #444C68;
}

.report-item textarea {
  box-sizing: border-box;
  width: 100%;
  padding: 20px 30px;
  line-height: 44px;
  font-size: 24px;
  color: #C4C4C4;
  outline: none;
  border: none;
}

.report-item p {
  padding-right: 30px;
  line-height: 80px;
  font-size: 24px;
  color: #C4C4C4;
  text-align: right;
}

.report-btn {
  padding-top: 100px;
  text-align: center;
}

.report-btn button {
  width: 660px;
  height: 90px;
  font-size: 36px;
  color: #fff;
  background-color: #FF6377;
  border-radius: 45px;
  outline: none;
  border: none;
}

.report-statement {
  padding: 250px 40px 40px;
  text-align: center;
}

.report-statement dt {
  line-height: 62px;
  font-size: 26px;
  font-weight: bold;
  color: #444C68;
}

.report-statement dd {
  line-height: 46px;
  font-size: 26px;
  color: #444C68;
}
</style>



