<template>
  <div id="suggest">
    <section class="suggest-item">
      <h3>您想提的意见</h3>
      <textarea
        rows="8"
        placeholder="请大致描述问题"
        maxlength="100"
        @input="contHandler"
      ></textarea>
      <p>{{cont.length}}/100</p>
    </section>
    <section class="suggest-item">
      <h3>你的电子邮箱</h3>
      <input
        type="text"
        placeholder="请填写您的邮箱，方便我们联系您"
        @input="mailHandler"
      />
    </section>
    <section class="suggest-btn">
      <button @click="submitHandler">提交</button>
    </section>
  </div>
</template>

<script>

export default {
  data () {
    return {
      cont: '',
      mail: ''
    }
  },
  computed: {},
  methods: {
    contHandler (e) {
      this.cont = e.target.value
    },
    mailHandler (e) {
      this.mail = e.target.value
    },
    submitHandler () {
      if ((!this.cont || !this.mail) && location.search.indexOf("iPhone") != -1) {
        alert('请完善反馈信息')
        window.JsBridge.showToast('请完善反馈信息')
        return false
      }
      if ((!this.cont || !this.mail) && location.search.indexOf("iPhone") == -1) {
        alert('请完善反馈信息')
        jsBridge.showToast('请完善反馈信息')
        return false
      }

      if (this.mail.indexOf("@") == -1) {
        alert('请填写正确的邮箱地址')
        window.JsBridge.showToast('填写正确的邮箱地址')
        return false
      }

      this.$http.post('http://h5.anyueclub.com/api/report/add_feedback' + location.search, {
        content: this.cont,
        email: this.mail,
        uid: this.getUrlQuery('uid')
      }).then((res) => {
        alert('感谢您的反馈，我们会尽快处理您的问题')
        if(location.search.indexOf("iPhone") != -1){
           window.JsBridge.confirmDialog('感谢您的反馈，我们会尽快处理您的问题')
        }else{
          jsBridge.confirmDialog('感谢您的反馈，我们会尽快处理您的问题')
        }
      }).catch((err) => {
        alert(err.error_msg || '反馈失败，请重试～')
        if(location.search.indexOf("iPhone") != -1){
          window.JsBridge.showToast(err.error_msg || '反馈失败，请重试～')
        }else{
         jsBridge.showToast(err.error_msg || '反馈失败，请重试～')
        }
      })
    },
    getUrlQuery (name) {
      let reg = new RegExp('(^|&)' + name + '=([^&]*)(&|$)', 'i')
      let search = location.search.substring(1)
      if (!search) return false
      let r = search.replace('#', '').match(reg)

      if (r != null) {
        let unescapeStr = unescape(r[2])
        switch (unescapeStr) {
          case 'true':
            return true
          case 'null':
            return null
          case 'false':
            return false
          case 'undefined':
            return undefined
          default:
            return unescapeStr
        }
      } else {
        return null
      }
    }
  }
}
</script>

<style scoped>
#suggest {
  width: 100%;
  height: 100%;
  background-color: #f7f7f7;
}

.suggest-item {
  padding-bottom: 20px;
}

.suggest-item h3 {
  padding-left: 30px;
  line-height: 90px;
  font-size: 30px;
  color: #444C68;
}

.suggest-item textarea {
  box-sizing: border-box;
  width: 100%;
  padding: 20px 30px;
  line-height: 44px;
  font-size: 24px;
  color: #C4C4C4;
  outline: none;
  border: none;
}

.suggest-item p {
  padding-right: 30px;
  line-height: 80px;
  font-size: 24px;
  color: #C4C4C4;
  text-align: right;
}

.suggest-item input {
  box-sizing: border-box;
  width: 100%;
  padding: 25px 30px;
  line-height: 50px;
  font-size: 24px;
  color: #C4C4C4;
  outline: none;
}

.suggest-btn {
  padding-top: 100px;
  text-align: center;
}

.suggest-btn button {
  width: 660px;
  height: 90px;
  font-size: 36px;
  color: #fff;
  background-color: #FF6377;
  border-radius: 45px;
  outline: none;
  border: none;
}
</style>



