import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import Suggest from '../page/suggest'
import Report from '../page/report'
import Privacy from '../page/privacy'
import Icebreaking from '../page/icebreaking'
import Contact from '../page/contact'

Vue.use(Router)

const router = new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'HelloWorld',
      component: HelloWorld,
      redirect: '/suggest'
    },
    {
      path: '/contact',
      name: 'contact',
      component: Contact,
      meta: {
        title: '联系我们'
      }
    },
    {
      path: '/privacy',
      name: 'privacy',
      component: Privacy,
      meta: {
        title: '隐私条款'
      }
    },
    {
      path: '/icebreaking',
      name: 'icebreaking',
      component: Icebreaking,
      meta: {
        title: '直男聊天宝典'
      }
    },
    {
      path: '/suggest',
      name: 'suggest',
      component: Suggest,
      meta: {
        title: '意见反馈'
      }
    },
    {
      path: '/report',
      name: 'report',
      component: Report,
      meta: {
        title: '匿名举报'
      }
    }
  ]
})

router.beforeEach((to, from, next) => {
  /* 路由发生变化修改页面title */
  if (to.meta.title) {
    document.title = to.meta.title
  }
  next()
})

export default router



